import { Component, OnInit } from '@angular/core';
import { ReminderService } from 'src/app/services/reminder.service';
import { AuthService } from 'src/app/services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MasterService } from 'src/app/services/master.service';
import { ToastaService } from 'projects/ngx-toasta/src/public_api';

@Component({
  selector: 'app-reminders',
  templateUrl: './reminders.component.html',
  styleUrls: ['./reminders.component.less']
})
export class RemindersComponent implements OnInit {

  public reminders: any = [];
  private show: boolean = false;
  private withdrawRequestId;


  constructor(private reminderService: ReminderService,
    private authService: AuthService, private router: Router,
    private masterService: MasterService,
    private toastaService: ToastaService,
    private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    if (this.authService.isAuthenticated()) {
      this.getMyReminders();
    } else {
      var toastOptions = this.masterService.setToastOptions('Reminders', 'Please login to continue', '')
      this.toastaService.error(toastOptions);
      let returnUrl: any = this._activatedRoute.snapshot;
      this.router.navigate(['/signin'], { queryParams: { returnUrl: returnUrl._routerState.url } });
    }
  }
  getMyReminders() {
    this.reminderService.getMyReminders().subscribe((reminderList: any) => {
      this.reminders = reminderList.Data;
      this.show = true;
    });

  }
  onDeleteClick(reminderId: number) {
    this.withdrawRequestId = reminderId;
  }
  onDeleteReminder() {
    var deletedReminder = this.reminders.filter(x => x.AlertsSchduleConfigId == this.withdrawRequestId)[0];
    deletedReminder.RecordDeleted=true;
    deletedReminder.Active=false;

    // var body =
    // {
    //   "AlertsSchduleConfigId": this.withdrawRequestId,
    //   "RecordDeleted": true,
    //   "Active": false,

    // }
    this.reminderService.updateReminder(deletedReminder).subscribe((result: any) => {
      let status = result.Status;
      if (status != "Errored") {
        var toastOptions = this.masterService.setToastOptions('Delete reminder', 'Reminder Successfully deleted', '')
        this.toastaService.success(toastOptions);
        this.getMyReminders();
      }
      else {
        var toastOptions = this.masterService.setToastOptions('Delete reminder', result.ErrorMessage, '')
        this.toastaService.error(toastOptions);
      }
    })
  }

}
