import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ToastaService } from 'projects/ngx-toasta/src/public_api';
import { MasterService } from 'src/app/services/master.service';
import { ReminderService } from 'src/app/services/reminder.service';

@Component({
  selector: 'app-addreminder',
  templateUrl: './addreminder.component.html',
  styleUrls: ['./addreminder.component.less']
})
export class AddreminderComponent implements OnInit {
  taskDescription: string = "";
  startDateTime: string = "";
  endDateTime: string = "";
  recurring: string = "true";
  recurringEvery: string = "Day";
  recurringPeriod: Number = 1;
  buttonName: string = "Save";
  constructor(private masterService: MasterService,
    private reminderService: ReminderService,
    private toastaService: ToastaService) { }

  ngOnInit() {
    this.startDateTime = this.masterService.getDateTimeProperFormat(new Date());
    this.endDateTime = this.masterService.getDateTimeProperFormat(new Date());
  }
  onSubmit(reminderForm: NgForm) {
    console.log(reminderForm);
    var body =
    {
      "TaskDescription": this.taskDescription,
      "Recurring": this.recurring,
      "StartDateTime": this.startDateTime,
      "EndDateTime": this.endDateTime,
      "RecurringEvery": this.recurringEvery,
      "RecurringPeriod": this.recurringPeriod
    }
    this.reminderService.createReminder(body).subscribe((res: any) => {
      let status = res.Status;
      if (status != "Errored") {
        var toastOptions = this.masterService.setToastOptions('Reminders', 'Success', 'reminders')
        this.toastaService.success(toastOptions);

      }
      else {
        var toastOptions = this.masterService.setToastOptions('Reminders', res.ErrorMessage, '')
        this.toastaService.error(toastOptions);
      }
    });
  }
}
