import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ReminderService {

  private apiUrl = environment.apiURL;
  private latitude: string;
  private longitude: string;
  constructor(private http: HttpClient) {
  }

  getMyReminders() {
    var body = {};
    return this.http.post(this.apiUrl + environment.apiEndPoints.getMySchedules, body)
  }

  createReminder(body: any) {
    return this.http.post(this.apiUrl + environment.apiEndPoints.configureASchedule, body)
  }

  updateReminder(body: any) {
    return this.http.post(this.apiUrl + environment.apiEndPoints.updateMySchedule, body)
  }
}
