import { Component } from '@angular/core';
import { MessagingServiceService } from './services/messaging-service.service';
import { DeviceDetectorService } from 'ngx-device-detector';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent {
  title = 'tst';
  message;
  deviceInfo = null;
  constructor(private _fcmPushService: MessagingServiceService, private deviceService: DeviceDetectorService) {
    this.deviceInfo = deviceService.getDeviceInfo();
    console.log(this.deviceInfo);

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          // Get current cordinates.
          //positionCords = { "lat": position.coords.latitude, "lng": position.coords.longitude };
          localStorage.setItem('latitude', position.coords.latitude.toString());
          localStorage.setItem('longitude', position.coords.longitude.toString());
        },
        (error) => {
          // On error code..
        },
        { timeout: 30000, enableHighAccuracy: true, maximumAge: 75000 }
      );
    }
    this._fcmPushService.getPermission();
   //this._fcmPushService.receiveMessage();
    this._fcmPushService.currentMessage.subscribe(
      (message) => { this.message = message; },
      (err) => { console.log(err) }
    );
  }
  onActivate() {
    window.scroll(0, 0);
  }
}
